<template>
  <div class="classic-browse-page">
    <div class="page-bg"></div>

    <div class="browse-inner">
      <aside class="sidebar">
        <div class="filter-section-wrap">
          <!-- 主题标签 (与 1.0 FilterSection 一致) -->
          <div class="filter-block">
            <div class="filter-controls">
              <h3 class="filter-controls-title">主题标签</h3>
              <div class="filter-controls-actions">
                <button
                  type="button"
                  class="btn-multi"
                  :class="{ active: isTagsMulti }"
                  @click="isTagsMulti = !isTagsMulti"
                >
                  多选
                </button>
                <button type="button" class="btn-clear" @click="clearTags">清空</button>
              </div>
            </div>
            <div class="tags-grid-2">
              <button
                v-for="tag in categoryList"
                :key="tag"
                type="button"
                class="tag-btn"
                :class="[getCategoryBtnClass(tag), { 'tag-btn-locked': isTagsLocked }]"
                :disabled="isTagsLocked"
                @click="handleTagSelect(tag)"
              >
                <span>{{ tag }}</span>
                <span class="tag-btn-count">{{ categoryCounts[tag] ?? 0 }}</span>
              </button>
            </div>
          </div>

          <!-- 体裁分类 (折叠，与 1.0 一致) -->
          <div class="collapsible border-t pt-4 mt-4">
            <button type="button" class="collapsible-head" @click="genreOpen = !genreOpen">
              <h3 class="collapsible-title">体裁分类</h3>
              <el-icon class="collapsible-icon" :class="{ rotate: genreOpen }"
                ><ArrowDown
              /></el-icon>
            </button>
            <Transition name="collapse">
              <div v-show="genreOpen" class="collapsible-body">
                <div class="genre-space">
                  <div v-for="main in genreMainList" :key="main" class="genre-group">
                    <button
                      type="button"
                      class="genre-main-btn"
                      :class="[getGenreMainClass(main), { 'tag-btn-locked': isGenresLocked }]"
                      :disabled="isGenresLocked"
                      @click="handleGenreMainSelect(main)"
                    >
                      <span>{{ main }}</span>
                      <span class="tag-btn-count">{{ primaryStats[main] ?? 0 }}</span>
                    </button>
                    <div class="genre-sub-grid">
                      <button
                        v-for="sub in getValidSubGenres(main)"
                        :key="sub"
                        type="button"
                        class="genre-sub-btn"
                        :class="[getGenreSubClass(sub), { 'tag-btn-locked': isGenresLocked }]"
                        :disabled="isGenresLocked"
                        @click="handleGenreSubSelect(sub)"
                      >
                        <span>{{ sub }}</span>
                        <span class="tag-btn-count">{{ genreStats[sub] ?? 0 }}</span>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </Transition>
          </div>

          <!-- 朝代筛选 (折叠) -->
          <div class="collapsible border-t pt-4 mt-4">
            <button type="button" class="collapsible-head" @click="dynastyOpen = !dynastyOpen">
              <h3 class="collapsible-title">朝代筛选</h3>
              <el-icon class="collapsible-icon" :class="{ rotate: dynastyOpen }"
                ><ArrowDown
              /></el-icon>
            </button>
            <Transition name="collapse">
              <div v-show="dynastyOpen" class="collapsible-body">
                <div class="filter-grid-3">
                  <button
                    v-for="d in sortedDynasties"
                    :key="d"
                    type="button"
                    class="dynasty-btn"
                    :class="{
                      active: selectedDynasties.includes(d),
                      'tag-btn-locked': isDynastiesLocked,
                    }"
                    :disabled="isDynastiesLocked"
                    @click="handleDynastySelect(d)"
                  >
                    <span>{{ d }}</span>
                    <span class="tag-btn-count">{{ dynastyCounts[d] ?? 0 }}</span>
                  </button>
                </div>
              </div>
            </Transition>
          </div>

          <!-- 作者筛选 (折叠) -->
          <div class="collapsible border-t pt-4 mt-4">
            <button type="button" class="collapsible-head" @click="authorOpen = !authorOpen">
              <h3 class="collapsible-title">作者筛选</h3>
              <el-icon class="collapsible-icon" :class="{ rotate: authorOpen }"
                ><ArrowDown
              /></el-icon>
            </button>
            <Transition name="collapse">
              <div v-show="authorOpen" class="collapsible-body">
                <div class="filter-grid-3">
                  <button
                    v-for="a in sortedAuthors"
                    :key="a"
                    type="button"
                    class="author-btn"
                    :class="{
                      active: selectedAuthors.includes(a),
                      'tag-btn-locked': isAuthorsLocked,
                    }"
                    :disabled="isAuthorsLocked"
                    @click="handleAuthorSelect(a)"
                  >
                    <span>{{ a }}</span>
                    <span class="tag-btn-count">{{ authorWorkCounts[a] ?? 0 }}</span>
                  </button>
                </div>
              </div>
            </Transition>
          </div>
        </div>

        <!-- 显示选项 (与 1.0 DisplaySettings 一致，独立于筛选卡片) -->
        <div class="display-settings-wrap">
          <div class="display-settings-grid">
            <div
              v-for="opt in toggleOptions"
              :key="opt.key"
              class="display-toggle-row"
              @click="opt.value = !opt.value"
            >
              <span class="display-toggle-label">{{ opt.label }}</span>
              <div class="display-switch" :class="{ checked: opt.value }">
                <div class="display-switch-handle"></div>
              </div>
            </div>
          </div>
        </div>
      </aside>

      <main class="main-content">
        <div
          v-if="!loading && items.length > 0 && totalPages > 1"
          ref="topPaginationRef"
          class="pagination-wrap pagination-top"
        >
          <div class="pagination-desktop">
            <el-pagination
              v-model:current-page="currentPage"
              :page-size="pageSize"
              :total="items.length"
              layout="prev, pager, next"
              small
              background
            />
          </div>
          <div class="pagination-mobile">
            <el-button
              size="small"
              :disabled="currentPage <= 1"
              @click="currentPage = Math.max(1, currentPage - 1)"
            >
              上一页
            </el-button>
            <span class="pagination-jump">
              <span class="pagination-jump-label">第</span>
              <el-input
                v-model.number="jumpPageInput"
                type="number"
                min="1"
                :max="totalPages"
                class="pagination-jump-input"
                @keyup.enter="goToPage"
              />
              <span class="pagination-jump-label">页 / 共 {{ totalPages }} 页</span>
              <el-button size="small" type="primary" @click="goToPage">跳转</el-button>
            </span>
            <el-button
              size="small"
              :disabled="currentPage >= totalPages"
              @click="currentPage = Math.min(totalPages, currentPage + 1)"
            >
              下一页
            </el-button>
          </div>
        </div>

        <div class="grid-wrap" v-if="!loading && items.length > 0">
          <router-link
            v-for="item in currentPageItems"
            :key="item.id"
            :to="`/classic/item/${item.id}`"
            class="item-card"
          >
            <div class="card-inner">
              <h3 v-if="getToggleValue('original')" class="card-original">
                {{ item.original_text }}
              </h3>
              <div v-if="getToggleValue('translation') && item.translation" class="card-trans-wrap">
                <p class="card-trans"><b>翻译：</b>{{ item.translation }}</p>
              </div>
              <div class="card-footer">
                <span class="card-meta">
                  <template v-if="getToggleValue('dynasty') && item.dynasty"
                    >［{{ item.dynasty }}］</template
                  >
                  <template v-if="getToggleValue('author') && item.author">{{
                    item.author
                  }}</template>
                  <template v-if="getToggleValue('author') && item.author && item.source">
                    ·
                  </template>
                  <template v-if="item.source">{{ item.source }}</template>
                  <span v-if="getToggleValue('style') && item.genre" class="card-genre">{{
                    item.genre
                  }}</span>
                </span>
                <div class="card-right">
                  <span class="card-likes">
                    <span class="icon-star"></span>
                    {{ item.likes ?? 0 }}
                  </span>
                  <template v-for="cat in itemCategories(item)" :key="cat">
                    <span :class="['category-tag', getCategoryTagClass(cat)]">{{ cat }}</span>
                  </template>
                </div>
              </div>
            </div>
          </router-link>
        </div>

        <div v-if="loading" class="loading-wrap">
          <el-icon class="is-loading" :size="32" color="#999"><Loading /></el-icon>
        </div>
        <div v-else-if="items.length === 0" class="empty-placeholder">
          <el-empty description="未找到相关素材，请调整筛选条件" />
        </div>

        <div v-if="!loading && items.length > 0 && totalPages > 1" class="pagination-wrap">
          <div class="pagination-desktop">
            <el-pagination
              v-model:current-page="currentPage"
              :page-size="pageSize"
              :total="items.length"
              layout="prev, pager, next"
              small
              background
            />
          </div>
          <div class="pagination-mobile">
            <el-button
              size="small"
              :disabled="currentPage <= 1"
              @click="currentPage = Math.max(1, currentPage - 1)"
            >
              上一页
            </el-button>
            <span class="pagination-jump">
              <span class="pagination-jump-label">第</span>
              <el-input
                v-model.number="jumpPageInput"
                type="number"
                min="1"
                :max="totalPages"
                class="pagination-jump-input"
                @keyup.enter="goToPage"
              />
              <span class="pagination-jump-label">页 / 共 {{ totalPages }} 页</span>
              <el-button size="small" type="primary" @click="goToPage">跳转</el-button>
            </span>
            <el-button
              size="small"
              :disabled="currentPage >= totalPages"
              @click="currentPage = Math.min(totalPages, currentPage + 1)"
            >
              下一页
            </el-button>
          </div>
        </div>
      </main>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onActivated, watch, nextTick } from 'vue'
import { ArrowDown, Loading } from '@element-plus/icons-vue'
import { browse } from '@/api/classic'
import type { ClassicItem } from '@/types/classic'
import { getNavigationFrom } from '@/router'

defineOptions({ name: 'ClassicBrowse' })

// --- 1.0 database_info 静态数据 (与 FilterSection 一致) ---
const categoryCounts: Record<string, number> = {
  格物修身: 270,
  成长成才: 185,
  道德情操: 339,
  哲理思辨: 593,
  济世情怀: 67,
  理想信念: 778,
  文化传承: 118,
  人与自然: 313,
  家国情怀: 126,
}
const categoryList = Object.keys(categoryCounts)

const dynastyCounts: Record<string, number> = {
  三国: 63,
  先秦: 100,
  南北朝: 36,
  唐: 757,
  宋: 593,
  明: 504,
  晋: 77,
  汉: 26,
}
const dynastyOrder = [
  '先秦',
  '汉',
  '三国',
  '晋',
  '南北朝',
  '隋',
  '唐',
  '五代',
  '宋',
  '辽',
  '金',
  '元',
  '明',
  '清',
  '近现代',
]
const dynastyList = Object.keys(dynastyCounts)
const sortedDynasties = [...dynastyList].sort(
  (a, b) =>
    (dynastyOrder.indexOf(a) >= 0 ? dynastyOrder.indexOf(a) : 999) -
    (dynastyOrder.indexOf(b) >= 0 ? dynastyOrder.indexOf(b) : 999)
)

const authorWorkCounts: Record<string, number> = {
  刘知几: 4,
  卢照邻: 16,
  周敦颐: 11,
  孔子: 100,
  孟浩然: 34,
  崔颢: 11,
  嵇康: 1,
  左思: 15,
  张旭: 3,
  张载: 32,
  文天祥: 22,
  曹丕: 21,
  曹操: 26,
  曹植: 25,
  曾巩: 18,
  朱熹: 26,
  李清照: 13,
  李白: 134,
  李邕: 1,
  杜甫: 353,
  杨炯: 15,
  梅尧臣: 4,
  欧阳修: 13,
  王之涣: 2,
  王安石: 27,
  王昌龄: 48,
  王绩: 67,
  程颐: 14,
  程颢: 11,
  苏洵: 18,
  苏轼: 59,
  苏辙: 26,
  谢朓: 8,
  谢灵运: 11,
  辛弃疾: 276,
  阮籍: 18,
  陆绍珩: 504,
  陶渊明: 60,
  骆宾王: 24,
  高适: 45,
  鲍照: 17,
  黄庭坚: 23,
}
const authorList = Object.keys(authorWorkCounts)
const sortedAuthors = [...authorList].sort(
  (a, b) => (authorWorkCounts[b] ?? 0) - (authorWorkCounts[a] ?? 0)
)

const genreMapping: Record<string, string[]> = {
  诗: [
    '七言律诗',
    '七言排律',
    '七言歌行',
    '七言绝句',
    '乐府诗',
    '五言律诗',
    '五言排律',
    '五言绝句',
    '古体诗',
  ],
  词: ['词'],
  曲: ['曲'],
  赋: ['赋'],
  文: ['书', '书启', '对策', '序', '文', '碑文', '表奏', '记', '论', '语录'],
}
const genreStats: Record<string, number> = {
  语录: 604,
  五言律诗: 255,
  七言律诗: 87,
  五言绝句: 35,
  七言绝句: 100,
  古体诗: 457,
  词: 227,
  论: 35,
  文: 166,
  五言排律: 109,
  七言排律: 2,
  乐府诗: 9,
  记: 28,
  书: 12,
  序: 9,
  七言歌行: 2,
  书启: 8,
  赋: 1,
  表奏: 3,
  对策: 1,
  碑文: 6,
}
const primaryStats: Record<string, number> = { 文: 872, 诗: 1056, 词: 227, 赋: 1 }
const genreMainList = Object.keys(genreMapping).filter((main) =>
  (genreMapping[main] ?? []).some((s) => (genreStats[s] ?? 0) > 0)
)

const toggleOptions = reactive([
  { key: 'original', label: '原文', value: true },
  { key: 'translation', label: '译文', value: true },
  { key: 'author', label: '作者', value: true },
  { key: 'dynasty', label: '朝代', value: true },
  { key: 'style', label: '体裁', value: false },
  { key: 'example', label: '范文', value: false },
])

// --- 响应式状态 ---
const activeTags = ref<string[]>(categoryList.length > 0 ? [categoryList[0]!] : [])
const selectedDynasties = ref<string[]>([])
const selectedAuthors = ref<string[]>([])
const selectedGenres = ref<string[]>([])

const isTagsMulti = ref(false)
const isTagsLocked = ref(false)
const isGenresLocked = ref(false)
const isDynastiesLocked = ref(false)
const isAuthorsLocked = ref(false)

const genreOpen = ref(false)
const dynastyOpen = ref(false)
const authorOpen = ref(false)

const items = ref<ClassicItem[]>([])
const loading = ref(false)
const pageSize = 15
const currentPage = ref(1)

// --- 计算属性 ---
const totalPages = computed(() => Math.ceil(items.value.length / pageSize) || 1)
const currentPageItems = computed(() => {
  const start = (currentPage.value - 1) * pageSize
  return items.value.slice(start, start + pageSize)
})

// 窄屏分页：跳转输入框与当前页同步
const jumpPageInput = ref(1)
watch(
  currentPage,
  (p) => {
    jumpPageInput.value = p
  },
  { immediate: true }
)
function goToPage() {
  const n = Number(jumpPageInput.value)
  if (!Number.isFinite(n) || n < 1) {
    currentPage.value = 1
    jumpPageInput.value = 1
    return
  }
  const max = totalPages.value
  const clamped = Math.min(max, Math.max(1, Math.floor(n)))
  currentPage.value = clamped
  jumpPageInput.value = clamped
}

// --- 辅助方法 ---
const getToggleValue = (key: string) => toggleOptions.find((o) => o.key === key)?.value

// 主题标签按钮样式名（与 1.0 getCategoryColor 对应，见 CSS .tag-cat-*-default / .tag-cat-*-selected）
const categoryBtnClassMap: Record<string, string> = {
  格物修身: 'purple',
  济世情怀: 'indigo',
  道德情操: 'sky',
  人与自然: 'cyan',
  哲理思辨: 'emerald',
  文化传承: 'lime',
  理想信念: 'amber',
  成长成才: 'orange',
  家国情怀: 'red',
}
function getCategoryBtnClass(tag: string): string {
  const key = categoryBtnClassMap[tag] ?? 'gray'
  const selected = activeTags.value.includes(tag)
  return `tag-btn tag-cat-${key}-${selected ? 'selected' : 'default'}`
}

// 体裁颜色 (1.0 getGenreColor)，用 CSS 类名
const genreMainKeyMap: Record<string, string> = {
  诗: 'blue',
  词: 'violet',
  曲: 'pink',
  赋: 'orange',
  文: 'emerald',
}
function getGenreColorFor(mainGenre: string): string {
  return genreMainKeyMap[mainGenre] ?? 'sky'
}
function getGenreMainClass(main: string): string {
  const key = getGenreColorFor(main)
  const valid = getValidSubGenres(main)
  const selected = valid.filter((g) => selectedGenres.value.includes(g))
  let state = 'default'
  if (selected.length === valid.length) state = 'full'
  else if (selected.length > 0) state = 'partial'
  return `genre-main-btn genre-${key} genre-main-${state}`
}
function getGenreSubClass(sub: string): string {
  let main = ''
  for (const [m, arr] of Object.entries(genreMapping)) {
    if (arr.includes(sub)) {
      main = m
      break
    }
  }
  const key = getGenreColorFor(main)
  const selected = selectedGenres.value.includes(sub)
  return `genre-sub-btn genre-${key} ${selected ? 'genre-sub-selected' : ''}`
}
function getValidSubGenres(main: string): string[] {
  const sub = genreMapping[main] ?? []
  return sub.filter((s) => (genreStats[s] ?? 0) > 0)
}

function itemCategories(item: ClassicItem): string[] {
  const raw = item.categories
  if (!raw) return []
  const arr = Array.isArray(raw)
    ? raw
    : String(raw)
        .split(',')
        .map((s: string) => s.trim())
  return arr.slice(0, 2)
}
const categoryTagMap: Record<string, string> = {
  格物修身: 'cat-purple',
  济世情怀: 'cat-indigo',
  道德情操: 'cat-sky',
  人与自然: 'cat-cyan',
  哲理思辨: 'cat-emerald',
  文化传承: 'cat-lime',
  理想信念: 'cat-amber',
  成长成才: 'cat-orange',
  家国情怀: 'cat-red',
}
function getCategoryTagClass(category: string): string {
  return categoryTagMap[category.trim()] ?? 'cat-gray'
}

// --- 筛选逻辑 (与 1.0 FilterSection 一致) ---
function clearTags() {
  activeTags.value = []
  applyFilters()
}
function handleTagSelect(tag: string) {
  if (isTagsLocked.value) return
  if (isTagsMulti.value) {
    const i = activeTags.value.indexOf(tag)
    if (i > -1) activeTags.value.splice(i, 1)
    else activeTags.value.push(tag)
  } else {
    activeTags.value = activeTags.value.includes(tag) ? [] : [tag]
  }
  applyFilters()
}
// 体裁分类：单选（仅能选一个子体裁或一个主类）
function handleGenreMainSelect(main: string) {
  if (isGenresLocked.value) return
  const valid = getValidSubGenres(main)
  if (valid.length === 0) return
  const allSelected = valid.every((g) => selectedGenres.value.includes(g))
  if (allSelected) {
    selectedGenres.value = []
  } else {
    selectedGenres.value = [...valid]
  }
  applyFilters()
}
function handleGenreSubSelect(sub: string) {
  if (isGenresLocked.value) return
  selectedGenres.value = selectedGenres.value.includes(sub) ? [] : [sub]
  applyFilters()
}
function handleDynastySelect(dynasty: string) {
  if (isDynastiesLocked.value) return
  selectedDynasties.value = selectedDynasties.value.includes(dynasty) ? [] : [dynasty]
  applyFilters()
}
function handleAuthorSelect(author: string) {
  if (isAuthorsLocked.value) return
  selectedAuthors.value = selectedAuthors.value.includes(author) ? [] : [author]
  applyFilters()
}

async function applyFilters() {
  loading.value = true
  try {
    const list = await browse({
      categories: [...activeTags.value],
      authors: [...selectedAuthors.value],
      dynasties: [...selectedDynasties.value],
      genres: [...selectedGenres.value],
    })
    items.value = list ?? []
    currentPage.value = 1
  } finally {
    loading.value = false
  }
}

// --- 生命周期与监听 ---

const topPaginationRef = ref<HTMLElement | null>(null)
const NARROW_BREAKPOINT = 768
const PAGINATION_SCROLL_OFFSET = 64
watch(currentPage, () => {
  nextTick(() => {
    if (window.innerWidth <= NARROW_BREAKPOINT && topPaginationRef.value) {
      const el = topPaginationRef.value
      const rect = el.getBoundingClientRect()
      const targetY = window.scrollY + rect.top - PAGINATION_SCROLL_OFFSET
      window.scrollTo({ top: Math.max(0, targetY), behavior: 'smooth' })
    } else {
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
  })
})

onActivated(() => {
  const from = getNavigationFrom()
  if (from?.name === 'ClassicItemDetail') return
  applyFilters()
})
</script>

<style scoped>
/* 字体设置 */
.classic-browse-page {
  position: relative;
  min-height: 100vh;
  font-family: 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', sans-serif;
  color: #333;
}

/* 1. 背景层 */
.page-bg {
  position: fixed;
  inset: 0;
  z-index: 0;
  background: url('https://images.unsplash.com/photo-1517488812739-865b61f938d6?q=80&w=2069&auto=format&fit=crop')
    no-repeat center center/cover;
}
.page-bg::before {
  content: '';
  position: absolute;
  inset: 0;
  background: rgba(255, 255, 255, 0.8);
}

/* 2. 核心布局 */
.browse-inner {
  position: relative;
  z-index: 1;
  max-width: 1280px;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
  gap: 40px;
  align-items: flex-start;
}

/* --- 左侧侧边栏 --- */
.sidebar {
  width: 280px;
  flex-shrink: 0;
  position: sticky;
  top: 80px;
}
/* 1.0 FilterSection 外壳 */
.filter-section-wrap {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 8px;
  box-shadow:
    0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -4px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(243, 244, 246, 0.5);
  transition: box-shadow 0.3s;
  padding: 16px;
  margin-bottom: 16px;
}
.filter-section-wrap:hover {
  box-shadow:
    0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 8px 10px -6px rgba(0, 0, 0, 0.1);
