/* 折叠块 (1.0 CollapsibleSection) */
.collapsible {
  border-color: rgba(229, 231, 235, 0.8);
  margin-top: 20px;
  padding-top: 20px;
}
.collapsible-head {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}
.collapsible-title {
  font-size: 18px;
  font-weight: 500;
  color: #111827;
  margin: 0;
  font-family: 'Source Han Serif CN', 'SimSun', 'STSong', serif;
}
.collapsible-icon {
  font-size: 20px;
  color: #6b7280;
  transition: transform 0.2s;
}
.collapsible-icon.rotate {
  transform: rotate(180deg);
}
.collapsible-body {
  margin-top: 12px;
  overflow: hidden;
  max-height: 2000px;
  transition:
    max-height 0.3s cubic-bezier(0.04, 0.62, 0.23, 0.98),
    opacity 0.3s cubic-bezier(0.04, 0.62, 0.23, 0.98);
}
.collapse-enter-active,
.collapse-leave-active {
  transition:
    max-height 0.3s cubic-bezier(0.04, 0.62, 0.23, 0.98),
    opacity 0.3s cubic-bezier(0.04, 0.62, 0.23, 0.98);
}
.collapse-enter-from,
.collapse-leave-to {
  opacity: 0;
  max-height: 0;
  margin-top: 0;
  overflow: hidden;
}

